<template>
     <div class="public_content">
        <div class="public_title"><span>更换手机</span></div>

        <v-code ref="oldPhone" label='当前手机号：'></v-code>
        <v-code ref="newPhone" label='新的手机号：'></v-code>
        
        <el-button type="primary" round @click="onSubmit" class="public_submit">确认更换</el-button>
     </div> 
</template>

<script>
import { editPhoneOne } from "@/api/login";
import vCode from "@/components/code/index";
export default {
  data() {
    return {
      new: false,
      old: false
    };
  },
  components: {
    vCode
  },
  computed: {},
  methods: {
    onSubmit() {
      if (this.$refs.oldPhone.form.phone && this.$refs.oldPhone.form.code && this.$refs.newPhone.form.phone && this.$refs.newPhone.form.code) {
        let data = {
          old_phone: this.$refs.oldPhone.form.phone,
          old_code: this.$refs.oldPhone.form.code,
          new_phone: this.$refs.newPhone.form.phone,
          new_code: this.$refs.newPhone.form.code
        };
        editPhoneOne(data).then(res => {
          if (res.data.code == 1) {
            this.$message.success("更改成功");
           } else {
            this.$message.success("修改失败");
          }
        });
      } else {
        this.$message.warning('信息填写不正确')
      }
    }
  }
};
</script>

<style lang='scss' scoped>
.public_tip {
  margin: 30px 0;
}
</style>



